@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700;900&family=Montserrat:wght@300;400;500;600;700;800&family=Roboto:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.header {
    display: flex;
    flex-direction: column;
    height: 7rem;
}

.header__top {
    display: flex;
    align-items: center;
    background-color: #1fafaa;
    width: 100%;
    padding: 0.6rem 4rem;
}

.header__top-logo {
    text-transform: uppercase;
    margin-bottom: 0;
    font-size: 20px;
    padding: 3px 13px;
    background-color: #f5f5f5;
    display: inline;
    border-radius: 4px;
    border: 1px solid #0e918c;
    color: #c7680a;
    font-family: "Montserrat";
}

.header__top-right {
    margin-left: auto;
    display: flex;
}

.header__top-account,
.header__top-cart {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 6px;
    color: #fff;
}

.header__top a:hover {
    text-decoration: none;
}

.header__top-account:hover,
.header__top-cart:hover {
    color: #d2d3c9;
}

.cart-items {
    position: relative;
    right: 12px;
    align-self: flex-start;
    padding: 1px 6px;
    background-color: rgb(31, 74, 109);
    color: rgb(248, 88, 29);
    border-radius: 20%;
}

.header__bottom {
    display: flex;
    align-items: center;
    background-color: #0e918c;
    padding: 0 4rem;
    height: 5rem;
    box-sizing: border-box;
}

.header__bottom .header__top-logo.bottom {
    background-color: #0e918c;
    color: #222;
}

.header__top-logo.bottom strong {
    display: block;
    font-size: 10px;
    text-align: center;
}

.header__bottom-nav {
    display: flex;
    align-items: center;
    justify-content: center;
}

.header__bottom-nav ul li {
    padding: 0.8rem 0;
    margin: 0 10px;
}

.header__bottom-nav ul li:hover {
    border-bottom: 2px solid #f5f5f5;
}

ul.header__bottom-links {
    display: flex;
    align-items: center;
    list-style: none;
}

ul.header__bottom-links {
    -webkit-margin-before: 0;
            margin-block-start: 0;
    -webkit-margin-after: 0;
            margin-block-end: 0;
    -webkit-margin-start: 0px;
            margin-inline-start: 0px;
    -webkit-margin-end: 0px;
            margin-inline-end: 0px;
    -webkit-padding-start: 0;
            padding-inline-start: 0;
    margin-left: 30px;
}

ul.header__bottom-links>a {
    color: #f5f5f5;
    font-size: 17px;
}

ul.header__bottom-links>a:hover {
    text-decoration: none;
}

.header__bottom-right {
    display: none;
    margin-left: auto;
}

.header__bottom-right.active {
    display: flex;
    margin-left: auto;
}

.header__bottom-account,
.header__bottom-cart {
    display: flex;
    align-items: center;
    padding: 0 6px;
    color: #fff;
    z-index: 1;
}

.header__bottom-account span {
    margin: 0 5px;
}

.cart-bottom-items {
    display: none;
    position: relative;
    right: 60px;
    font-weight: bold;
    top: -14px;
    border-radius: 4px;
    background-color: rgba(73, 122, 84, 0.5);
    padding: 0 6px;
    color: #ca8c8c;
}


/* OnScroll */

.onScroll {
    background-color: #9ecccb;
    position: fixed;
    top: 0;
    display: flex;
    align-items: center;
    padding: 0 4rem;
    height: 4rem;
    box-sizing: border-box;
    z-index: 1;
    width: 100%;
}

.onScroll .header__top-logo.bottom {
    background-color: rgb(204, 204, 204);
    color: #c7680a;
    letter-spacing: 1px;
}

.header-logo {
    display: flex;
}

.header-logo:hover {
    text-decoration: none;
}

.onScroll .cart-bottom-items {
    display: flex;
    background-color: #9ecccb;
    color: #222;
}

.onScroll .header__bottom-right.active a:hover {
    text-decoration: none;
}

.onScroll .header__bottom-right.active span {
    margin-left: 5px;
}

@media (max-width:557px) {
    .header {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    .header__top {
        display: flex;
        justify-content: space-between;
        padding: 0 3px;
    }
    .header__bottom {
        display: flex;
        justify-content: space-between;
        padding: 0;
    }
}
.home {
    height: 100vh;
}


/* Home Fasion */

.home__fashion-women {
    height: 30rem;
    background-repeat: no-repeat;
    width: 100%;
    background-size: cover;
}

.home__fashion-kids,
.home__fashion-men {
    height: 30rem;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: 100%;
    margin-top: 10px;
}

.home__fasion-bottom {
    display: flex;
    width: 100%;
}

.home__fashion-women {
    display: flex;
    justify-content: center;
}

.home__fasion-women-text {
    display: inline-flex;
    flex-direction: column;
    text-align: center;
    position: relative;
    top: 370px;
}

.home__fasion-text {
    padding-top: 400px;
    padding-left: 20px;
    display: inline-flex;
    flex-direction: column;
    text-align: center;
}

.home__fashion-title {
    text-transform: uppercase;
    font-size: 26px;
    font-weight: 600;
    color: #212;
}

.home__fashion-link {
    background-color: #555555;
    border-radius: 4px;
    color: #cdc9c3;
    text-transform: uppercase;
    cursor: pointer;
}

.home__fashion-link:hover {
    text-decoration: none;
    color: #555555;
    background-color: #cdc9c3;
}

footer {
    background-color: #637373;
    padding: 30px 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
}

.footer-left {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 70px;
    border-right: 1px solid grey;
    padding-right: 50px;
}

.footer-center {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 70px;
    border-right: 1px solid grey;
    padding-right: 50px;
}

.footer-right {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 70px;
}

.footer-right h4 {
    margin-bottom: 0;
}

.footer-left h4 {
    font-size: 20px;
    color: #302e2e;
}

.footer-icons {
    display: flex;
    justify-content: space-around;
    padding: 0 42px;
    align-items: center;
}

.footer-search {
    margin-left: 15px;
}

.footer-search>input {
    border: none;
    background-color: #637373;
    text-align: center;
    border-bottom: 1px solid rgb(82, 82, 82);
}

.footer-search>input:focus {
    outline: none;
}

.footer-search>span {
    background-color: #212;
    display: inline-flex;
    padding: 0 10px;
    text-transform: uppercase;
    color: #f8f8f8;
    border-radius: 16px;
    cursor: pointer;
}

@media (max-width:1227px) {
    footer {
        display: inline-block;
    }
    .footer-left {
        display: inline-flex;
        width: 50%;
        border: none;
    }
    .footer-center {
        display: inline-flex;
        width: 50%;
        border: none;
    }
    .footer-right {
        display: block;
        margin-top: 20px;
        text-align: center;
    }
    .footer-search {
        margin-top: 10px;
    }
    .footer-search>input {
        width: 30%;
    }
}

@media (max-width:776px) {
    .home {
        height: auto;
    }
    footer {
        display: block;
    }
    .footer-left {
        width: 100%;
        text-align: center;
        border-bottom: 1px solid grey;
        padding: 25px 0;
    }
    .footer-center {
        width: 100%;
        text-align: center;
        border-bottom: 1px solid grey;
        padding: 25px 0;
    }
}

@media (max-width:557px) {
    .home__fasion {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .home__fashion-women {
        background-position: center center;
        background-size: contain;
    }
    .home__fashion-kids {
        background-size: contain;
    }
    .home__fashion-men {
        background-size: contain;
        width: 95%;
        margin-left: 0;
    }
    .home__banner {
        background-size: contain;
    }
    .home__fasion-bottom {
        display: flex;
        flex-direction: column;
    }
}

.footer-left svg {
    margin: 0 10px;
}

::-webkit-input-placeholder {
    color: rgb(63, 60, 60);
}

:-ms-input-placeholder {
    color: rgb(63, 60, 60);
}

::-ms-input-placeholder {
    color: rgb(63, 60, 60);
}

::placeholder {
    color: rgb(63, 60, 60);
}

:-ms-input-placeholder {
    color: rgb(54, 48, 48);
}

::-ms-input-placeholder {
    color: red;
}
.homeProduct {
    margin-top: 40px;
}

.homeProduct-title {
    text-transform: uppercase;
    font-family: 'Montserrat', sans-serif;
}

.homeProduct__img {
    width: 224px;
    height: 200px;
    object-fit: contain;
    background-color: #fff8;
}

.homeProduct-items {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    padding: 10px 0;
}

.homeProduct-item {
    text-align: center;
    background-color: #d3d3d3d3;
    margin: 2.5px;
}

.homeProduct-item:hover .homeProduct__img {
    opacity: 0.3;
}

.homeProduct-rating {
    display: flex;
    justify-content: center;
    font-size: 10px;
}

.homeProduct-item:hover .homeProduct-info {
    background-color: #d3d3d3d3;
    opacity: 0.3;
}

.homeProduct-item:hover .homeProduct-info {
    display: none;
    background-color: #fff8;
    opacity: 0.3;
}

.homeProduct-item>span {
    color: #0e918c;
}

.ToTheCart {
    display: none;
}

.ToTheCart>a {
    padding: 2px 10px;
    background-color: #fff;
    color: #d19152;
    border-radius: 4px;
    margin-top: 10px;
    border: 0.5px solid;
    border-color: #f6830f #f6630f #f6820f;
    ;
}

.ToTheCart>a:hover {
    text-decoration: none;
}

.homeProduct-item:hover .ToTheCart {
    display: flex;
    justify-content: center;
}

.home__banner {
    display: inline-block;
    position: relative;
    background-image: url("http://wordpress.templatemela.com/woo/WCM05/WCM050104/wp-content/uploads/2019/06/Main-Banner-02.jpg");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    width: 100%;
    height: 100%;
    visibility: inherit;
    z-index: 20;
    transition: all .3s ease;
}

.home__banner:hover {
    background-size: 110%;
}

.home__banner-text {
    position: absolute;
    bottom: 20%;
    left: 20px;
}

.home__banner-text h1 {
    text-transform: uppercase;
    color: rgb(95, 165, 165);
    font-size: 34px;
    font-weight: bold;
}

.home__banner-text p {
    font-size: 50px;
}

.home__banner-text p strong {
    font-size: 66px;
}

.home__banner-btn {
    border-radius: 4px;
    text-transform: uppercase;
    cursor: pointer;
    background-color: #f56a79;
    padding: 2px 6px;
    color: #d9ecf2;
}

.home__banner-btn:hover {
    color: #f56a79;
    background-color: #fff;
    border: 1px solid #0e918c;
    text-decoration: none;
}

@media (max-width:557px) {
    .home__banner {
        background-size: contain;
    }
}
.shopApp {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Lato', sans-serif;
}
